import React, {Component, Fragment} from 'react';

class About extends Component {
    createMarkUp(html) {
        return {__html: html}
    }


    render() {
        const {data, isLoading} = this.props.data;
        if (isLoading) {
            return (<p>Loading ...</p>)
        }
        else {
            const summary3List = data.summary3.map((summary, index) => 
                <li key={index}>{summary}</li>
            );
        return (
            <section id="about" className="section-padding">
            <div className="container">
                <div className="row">
                <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                    <div className="profile-wrapper wow fadeInRight" data-wow-delay="0.3s">
                        <h3>Hi Guys!</h3>
                        <p>{data.summary1}</p>
                        <p dangerouslySetInnerHTML={{__html: data.summary2}}></p>
                        <ul className="line-text summarylist">{summary3List}</ul>
                        <div className="about-profile">
                            <ul className="admin-profile">
                            <li><span className="pro-title"> Name </span> <span className="pro-detail">{data.name}</span></li>
                            <li><span className="pro-title"> Experience </span> <span className="pro-detail">{data.experience}</span></li>
                            <li><span className="pro-title"> Country </span> <span className="pro-detail">{data.country}</span></li>
                            <li><span className="pro-title"> e-mail </span> <span className="pro-detail">{data.email}</span></li>
                            <li><span className="pro-title"> Phone </span> <span className="pro-detail">{data.phone}</span></li>
                            </ul>
                        </div>
                    </div>
                </div>   
                </div>
            </div>
            </section>
            )
        }
    }
}

export default About;