import React, {Component} from 'react';
import ContactForm from './ContactForm';

class Contact extends Component {
    render() {
        const {data, isLoading} = this.props.data;

        if (isLoading) {
            return (<p>Loading ...</p>)
        }
        else {
            return (
                <section id="contact" className="section-padding">      
                    <div className="contact-form">
                        <div className="container">
                        <div className="row contact-form-area wow fadeInUp" data-wow-delay="0.4s">          
                            <div className="col-md-6 col-lg-6 col-sm-12">
                                {/*<ContactForm />*/}
                            </div>
                            <div className="col-md-6 col-lg-6 col-sm-12">
                            <div className="footer-right-area wow fadeIn">
                                <h2>Contact Address</h2>
                                <div className="footer-right-contact">
                                <div className="single-contact">
                                    <div className="contact-icon">
                                    <i className="fa fa-map-marker"></i>
                                    </div>
                                    <p>{data.country}</p>
                                </div>
                                <div className="single-contact">
                                    <div className="contact-icon">
                                    <i className="fa fa-envelope"></i>
                                    </div>
                                    <p><a href={`mailto:${data.email2}`}>{data.email2}</a></p>
                                    <p><a href={`mailto:${data.email}`}>{data.email}</a></p>
                                </div>
                                <div className="single-contact">
                                    <div className="contact-icon">
                                    <i className="fa fa-phone"></i>
                                    </div>
                                    <p><a href="#">{data.phone}</a></p>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>   
                </section>
            )
        }
    }
}

export default Contact;