import React, {Component} from 'react';

class Resume extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoading : false,
            data : {
                educations : [],
                experiences : []
            }
        }
    }

    componentDidMount() {
        console.log('mounting');
        this.setState({isLoading : true});
        fetch('./data/resume.json')
        .then((res) => res.json())
        .then((json) => {
            //console.log('data:', json.data);
            this.setState({
                data : json.data,
                isLoading: false
            })
        })
    }

    render() {
        const {data, isLoading} = this.state;

        if (isLoading) {
            return (<p>Loading ...</p>)
        }
        else {

            const educationList = data.educations.map((education) => 
                    <li key={education.major}>
                    <div className="content-text">
                        <h3 className="line-title">{education.major} - {education.school}</h3>
                        <span>{education.year}</span>
                        <p className="line-text">{education.result}</p>
                    </div>
                </li>
                );
            
            const experienceList = data.experiences.map((experience) => 
                <li key={experience.company}>
                <div className="content-text">
                    <h3 className="line-title">{experience.role} - {experience.company}</h3>
                    <span><b>Project : </b>{experience.project}<br/>{experience.tenure}</span>
                        <ul className="line-text bullet-list">
                            <li></li>
                            {experience.responsibilities.map(function(name, index){
                                return <li key={index}>{name}</li>;
                            })}
                        </ul>
                </div>
                </li>
                );

            return (
                <div id="resume" className="section-padding">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-6 col-md-6 col-sm-6">
                                <div className="experience wow fadeInRight" data-wow-delay="0.6s">
                                    <ul className="timeline">
                                        <li>
                                        <i className="icon-briefcase"></i>
                                        <h2 className="timelin-title">Experience</h2>
                                        </li>
                                        {experienceList}
                                    </ul>
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6 col-sm-6">
                            
                                <div className="education wow fadeInRight" data-wow-delay="0.3s">
                                    <ul className="timeline">
                                        <li>
                                            <i className="icon-graduation"></i>
                                            <h2 className="timelin-title">Education</h2>
                                        </li>
                                        {educationList}
                                    </ul>
                                </div>                           
                            </div>
                        </div>
                     </div>
                </div>
            )
        }
    }
}

export default Resume;
