import React, {Component} from 'react';

class Work extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoading : false,
            data : {
                developments : [],
                photographys : []
            }
        }
    }

    componentDidMount() {
        console.log('mounting');
        this.setState({isLoading : true});
        fetch('./data/work.json')
        .then((res) => res.json())
        .then((json) => {
            //console.log('data:', json.data);
            this.setState({
                data : json.data,
                isLoading: false
            })
        })
    }

    render() {
        const {data, isLoading} = this.state;

        if (isLoading) {
            return (<p>Loading ...</p>)
        }
        else {
            const developmentList = data.developments.map((development) => 
                    <div className="col-sm-6 col-md-4 col-lg-4 col-xl-4 mix development print" key={development.title}>
                        <div className="portfolio-item">
                            <div className="shot-item">
                            <img src={`assets/img/apps/${development.icon}`} alt={development.title} styleclass="preview-works"/> 
                            <div className="overlay">
                                <div className="icons">
                                <a className="lightbox preview" href={`assets/img/apps/${development.icon}`}>
                                    <i className="icon-eye"></i>
                                </a>
                                <a className="link" href={development.link} target="_blank">
                                    <i className="icon-link"></i>
                                </a>
                                </div>
                            </div>
                            </div>               
                        </div>
                    </div>
                );

            const photographyList = data.photographys.map((photography) => 
                <div className="col-sm-6 col-md-4 col-lg-4 col-xl-4 mix photography print" key={photography.title}>
                    <div className="portfolio-item">
                        <div className="shot-item">
                        <img src={`assets/img/photo/${photography.icon}`} alt={photography.title}/> 
                        <div className="overlay">
                            <div className="icons">
                            <a className="lightbox preview" href={`assets/img/photo/${photography.icon}`}>
                                <i className="icon-eye"></i>
                            </a>
                            </div>
                        </div>
                        </div>               
                    </div>
                </div>
            );

            return (
                <section id="portfolios" className="section-padding">
                    <div className="container">        
                        <h2 className="section-title wow flipInX" data-wow-delay="0.4s">My Remarkable Works</h2>
                        <div className="row">          
                            <div className="col-md-12">
                                <div className="controls text-center">
                                {/*<a className="filter btn btn-common" data-filter=".design">
                                    Design 
                                </a>*/}
                                <a className="filter active btn btn-common" data-filter=".development">
                                    Development
                                </a>
                                <a className="filter btn btn-common" data-filter=".photography">
                                    PhotoGraphy 
                                </a>
                                <a className="filter btn btn-common" data-filter="all">
                                    All 
                                </a>
                                </div>
                            </div>

                            <div id="portfolio" className="row wow fadeInDown" data-wow-delay="0.4s">
                                {developmentList}
                                {photographyList}
                            </div>
                        </div>
                    </div>
                </section>
            )
        }
    }
}

export default Work;